import React, { useContext } from 'react';
import {Link, NavLink} from 'react-router-dom';
import { AuthContext } from '../AuthContext';

import URL from 'constants/url';

function EgovHeader() {

    const { isLoggedIn, sessionUserName } = useContext(AuthContext);

    return (
        <div className="header">
            <div className="inner">

                <h1 className="logo">
                    <Link to={URL.MAIN} className="w"><img src="/assets/images/logo.svg" alt=" eGovFrame " /></Link>
                    <Link to={URL.MAIN} className="m"><img src="/assets/images/logo.svg" alt=" eGovFrame " /></Link>
                </h1>

                <div className="gnb">
                    <h2 className="blind"></h2>
                    <ul>
                        {/* <li><NavLink to={URL.ABOUT} className={({ isActive }) => (isActive ? "cur" : "")}></NavLink></li> */}
                        {isLoggedIn &&
                        <li><NavLink to={URL.USERINFO} > </NavLink></li>
                        }
                    </ul>
                </div>

                <div className="user_info">
                    {/*  :    */}
                    {isLoggedIn &&
                        <>
                            <span className="person">{sessionUserName} </span>
                            <a href="/logout">
                                <button className="btn logout"></button>
                            </a>
                        </>
                    }
                    {/*  :     */}
                    {!isLoggedIn &&
                        <a href="/oauth2/authorization/google">
                            <button className="btn login"></button>
                        </a>
                    }
                </div>
            </div>

            {/* <!-- All menu : mobile --> */}
            <div className="all_menu Mobile closed">
                <div className="user_info_m">
                    {/*  :    */}
                    {isLoggedIn &&
                        <>
                            <span className="person">{sessionUserName} </span> ,  .
                            <a href="/logout">
                                <button className="btn logout"></button>
                            </a>
                        </>
                    }

                    {/*  :     */}
                    {!isLoggedIn &&
                        <a href="/oauth2/authorization/google">
                            <button className="btn login"></button>
                        </a>
                    }
                </div>
            </div>
        </div>
    );
}

export default EgovHeader;